@import "../../styles/mixins";
$item-space: 20px;

.counter{
	@include border-and-shadow();

	position: fixed;
	top: 0;
	display: flex;
	width: 100%;
	flex-direction: column;
	align-items: center;
	background-color: white;
	padding-top: 20px;
	z-index: 1;
	&__item{
		@include border-and-shadow();
		position: relative;
		margin: $item-space;
		width: 122px;
		height: 33px;
		border: solid 2px #6d6e71;

		box-sizing: border-box;
		.duck-wrapper{
			@include border-and-shadow();
			position: absolute;
			top: -2px;
			left: -2px;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 65px;
			height: 33px;
			background-color: #ff7b01;
			border: none;
			svg{
				width: 21px;
				height: 21px;
				color: white;
			}
		}
		p{
			margin: 0;
		}
		.number{
			position: absolute;
			top: -2px;
			left: 65%;
			font-size: 1.9rem;
			letter-spacing: -.015em;
			color: #ff7b01;
		}
	}
	.title{
		position: absolute;
		top: 0;
		color: grey;
		text-transform: uppercase;
	}

	.mute-button{
		@include border-and-shadow();
		position: absolute;
		top: -1px;
		left: -100px;
		display: flex;
		color: #EF8132;
		background-color: white;

		padding: 7px 4px;

		svg{
			width: 24px;
			height: 18px;
		}
	}
}
