@import "../../styles/mixins";

.buttons-wrapper{
	display: flex;
	justify-content: space-around;
	margin-top: 12px;
	svg{
		display: block;
		width: 1.5rem;
		height: 1.5rem;
	}
}

.question-helper{
	display: flex;
	margin: 20px 0;
	p{
		font-size: 2rem;
		margin: 20px auto;
	}
}

.btn-custom{
	@include border-and-shadow();
	font-size: 2rem;
	border: 2px solid #ff7b01;
	padding: 5px 20px;
	background-color: white;
	&:hover,&:active{
		background-color: #ff7b01;
		color: white;
	}
}
