.counter-tooltip{
	position: absolute;
	top: 0;
	left: -110px;
	font-size: 6.5rem;
	border-radius: 50%;
	opacity: 1;
	z-index: 2;
	visibility: visible;

	background-color: #ff7b01;
	color: white;
	padding: 20px 30px;
	transform: scale(.4);
	svg{
		width: 100px;
		height: 70px;
		transform: scale(1);
	}
	&.active{
		transition: all .7s;
		transform: translate3d(0, -50px, 0);
		opacity: 0;
		visibility: hidden;
	}
}
