@mixin button-bg($bg-color) {
	background: $bg-color;
	&:hover {
		background:darken($bg-color,8%);
		transition: all 0.3s ease;
	}
	&:active {
		background:darken($bg-color,25%);
	}
}

@mixin border-and-shadow{
	border: solid 1px #e6e6e6;
	border-radius: 7px;
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.05);
}
