@import "../../styles/vars";

.was-preloader{
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	svg{
		width: 140px;
		height: 140px;
		fill: none;

		stroke-width: 1;
		stroke-linecap: round;
		stroke-linejoin: round;
		stroke-miterlimit: 1000;
		stroke-dasharray: 1500;
		stroke-dashoffset: 1500;
		animation: croco-dash 6s ease-in alternate infinite;
	}
}

@keyframes rotating {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(1800deg);
	}
}

@keyframes croco-dash {
	100% {
		stroke-dashoffset: 0;
	}
}
