.swiper-card{
	position: relative;
	user-select: none;
	padding: 10px;
	width: 300px;
	cursor: move;

	p{
		font-size: 20px;
		user-select: none;
		margin: 20px 0;
	}

	@media (min-width: 720px) {
		width: 400px;
	}

		.img {
		width: 300px;
		height: 300px;
		background-size: cover;
		background-position: center;
		@media (min-width: 720px) {
			width: 400px;
			height: 400px;
		}
	}
}
