@import '../../styles/vars';
@import '../../styles/mixins';
@import '../../assets/fonts/fonst';

body{
	font-family: Din;
	font-size: $base-font;
}

button{
	cursor: pointer;
	border: none;
}

.btn {
	color: white;
	text-decoration: none;
	padding: 12px;
	border-radius: 50%;
	font-size: 2em;

	border: none;
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.05);
	background-color: #ffffff;
}

.btn-red{
	color: #ff7b01;
}

.wrapper-styles{
	@include border-and-shadow();
	overflow: hidden;
}

.st0{fill:url(#SVGID_1_);}
